.body-player {
  background: url(../images/bg-report.png) no-repeat;
  background-size: cover;
  background-position: top;
  overflow-y: auto;
  height: calc(100vh - 80px);
  .contenedor-search {
    background-size: cover;
    padding: 100px 1rem 0 1rem;
    border-radius: 0 0 35px 35px;
    @media (min-width: 525px) {
      padding: 150px 1rem 0 1rem;
    }
    .search {
      display: flex;
      align-items: center;
      position: relative;
      .icon-search {
        position: absolute;
        right: 0;
        transform: translateX(-140%);
        width: 6%;
        @media (min-width: 525px) {
          width: 3%;
        }
      }
      .form-control {
        background: #fff;
        line-height: 2;
        border-color: transparent;
        border-radius: .7rem;
        font-size: .8rem;
        // margin-right: .7rem;
        padding: .6rem .8rem;
        &:focus {
          background: #fff;
          border-color: transparent;
          box-shadow: 0 0 0 2px rgb(194, 206, 202);
          color: #EB8950;
        }
      }
    }
  }
  .contenedor-ppal {
    padding: 1rem;
    background-color: #fff;
    margin: 1rem;
    border-radius: 10px;
    .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
      width: 100%
    }
    .number {
      font-size: .8rem;
    }
    label {
      font-size: .8rem;
    }
    .form-label {
      margin-bottom: 0;
    }
    .form-control {
      font-size: .8rem;
      padding: 0.3rem .75rem;
      background-color: #e9eef1;
      &:focus {
        border-color: none;
        box-shadow: none;
      }
    }
    .bootstrap-select {
      .btn {
        padding: .19rem .75rem;
        border: 1px solid #ced4da;
        background-color: #e9eef1;
      }
    }
    .contenedor-level {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .checkeable {
        flex: 0 0 30%;
        margin-bottom: .5rem;
        .level {
          background-color: #e9eef1;
          padding: .3rem 1rem;
          text-align: center;
          border-radius: 5px;
          span {
            font-size: .8rem;
          }
        }
      }
      .checkeable input {
        display: none;
      }
      .checkeable input:checked + .level {
        background-color: #EB8950;
        color: #fff;
      }
    }
    .title-player {
      color: #EB8950;
      font-weight: bold;
      margin-bottom: .3rem;
    }
    .contenedor-input {
      display: flex;
      .col-izq {
        flex: 0 0 40%;
      }
      .col-der {
        flex: 0 0 60%;
        margin-left: 1rem;
        .form-select {
          font-size: .8rem;
          background-color: #e9eef1;
          color: rgb(85, 81, 81);
          width: 88%;
          margin-top: .07rem;
          &:focus {
            border-color: none;
            box-shadow: none;
          }
        }
      }
      .fileUpload {
        background: #e9eef1;
        border-radius: 5px;
        color: rgb(85, 81, 81);
        font-size: .8rem;
        overflow: hidden;
        padding: 2rem 1rem;
        position: relative;
        text-align: center;
        cursor: pointer;
        &:hover {
          background: #e9eef1;
          cursor: pointer;
        }
        &:active {
          background: #e9eef1;
          cursor: pointer;
        }
        &:focus {
          background: #e9eef1;
          cursor: pointer;
        }
        input {
          position: absolute;
          top: 0;
          right: 0;
          margin: 0;
          padding: 0;
          font-size: 20px;
          cursor: pointer;
          opacity: 0;
          filter: alpha(opacity=0);
          width: 148px;
          height: 46px;
          cursor: pointer;
        }
      }
    }
  }
  .botones {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem;
    padding-bottom: 80px;
    .btn {
      padding: .8rem 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-weight: bold;
      border-radius: 10px;
      &:focus {
        border-color: none;
        box-shadow: none;
      }
      img {
        width: 10%;
        margin-right: 1rem;
      }
    }
    .btn-back {
      background-color: rgb(87, 81, 81);
      a {
        text-decoration: none;
        color: #fff;
      }
    }
    .btn-add {
      background-color: #EB8950;
    }
  }
}
.search input::placeholder {
  color: rgb(172, 166, 166);
}
input[type="file"] {
  position: fixed;
  right: 100%;
  bottom: 100%;
}
.custom-file-upload {
  border: 1px solid #ccc;
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
}